import React from 'react'

const Alert = ({activeAlert}) => {
    if (activeAlert === 'confirmation') {
        return (
        <div className='alert-confirmation'>
            <div>&#10003;</div>
            <div>Požadavek byl potvrzen.</div>
        </div>  
        )
    } else if (activeAlert === 'pending') {
        return (
        <div className='alert-pending'>
            <div>59:59</div>
            <div>
                <div>Potvrzení požadavku</div>
                <div className='alert-pending-additional-info'><a href='/#'>Odeslat SMS</a> s potvrzovacím kódem.</div>
            </div>
        </div>
        )
    } else if (activeAlert === 'rejection') {
        return (
            <div className='alert-rejected'>
                <div>&#x2715;</div>
                <div className='alert-rejected-text-container'>
                    <div>Požadavek nebyl potvrzen.</div>
                    <div className='alert-rejected-additional-info'><a href='/#'><span className='no-underline'>&#8635;</span>Zkusit znovu</a></div>
                </div>
            </div>
            )
    } else {
        return null
    }
}

export default Alert