import React, {useState} from 'react'
import Alert from './Alert'

const App = () => {
  const [activeAlert, setActiveAlert] = useState('')

  const handleClick = (event) => {
    setActiveAlert(event.target.value)
  }

  return (
    <div>
      <header style={{textAlign: 'center'}}>
        <h1>Presentation of Alerts/Buttons:</h1>
      </header>
      <main style={{margin: 10, textAlign: 'center'}} >
        <button style={{backgroundColor: 'rgb(51, 130, 79)', color: 'white'}} onClick={handleClick} value='confirmation'>CONFIRMATION</button>
        <button style={{backgroundColor: 'rgb(24, 5, 99)', color: 'white', margin: 10}} onClick={handleClick} value='pending'>PENDING</button>
        <button style={{backgroundColor: 'rgb(208, 19, 27)', color: 'white'}} onClick={handleClick} value='rejection'>REJECTION</button>
      </main>
      <footer>
        <Alert activeAlert={activeAlert}/>
      </footer>
    </div>
  )
}

export default App